import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const spareBaseUrl = BasePaths.Spare;

const definedContributionsPathDefinitions = {
  calculations: '/defined-contributions/{agreementId}/calculations',
  definedContributions: '/defined-contributions',
  definedContributionsFromAgreement:
    '/defined-contributions/?agreementId={agreementId}',
  epkFlexCalculations: '/defined-contributions/epk-flex/calculations',
  epkFlexDefinedContributions: '/defined-contributions/epk-flex',
  epkFlexInvestmentProducts:
    '/defined-contributions/epk-flex/investment-products',
  epkFlexPerformanceOverview:
    '/defined-contributions/epk-flex/performance-overview',
  epkFlexTransactions: '/defined-contributions/epk-flex/transactions',
  investmentProducts:
    '/defined-contributions/{agreementId}/investment-products',
  performanceOverview:
    '/defined-contributions/{agreementId}/performance-overview',
  transactions: '/defined-contributions/{agreementId}/transactions',
};

const definedContributionsEndpoints = {
  definedContributions: () =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.definedContributions
    ),

  definedContributionsFromAgreement: (agreementId: string) =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.definedContributionsFromAgreement,
      { agreementId: agreementId }
    ),

  investmentProducts: (agreementId: string) =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.investmentProducts,
      { agreementId: agreementId }
    ),

  calculations: (agreementId: string) =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.calculations,
      { agreementId: agreementId }
    ),

  performanceOverview: (agreementId: string) =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.performanceOverview,
      { agreementId: agreementId }
    ),
  transactions: (agreementId: string) =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.transactions,
      { agreementId: agreementId }
    ),
  epkFlexCalculations: () =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.epkFlexCalculations
    ),
  epkFlexDefinedContributions: () =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.epkFlexDefinedContributions
    ),
  epkFlexTransactions: () =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.epkFlexTransactions
    ),
  epkFlexPerformanceOverview: () =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.epkFlexPerformanceOverview
    ),
  epkFlexInvestmentProducts: () =>
    buildEndpoint(
      spareBaseUrl,
      definedContributionsPathDefinitions.epkFlexInvestmentProducts
    ),
};

export { definedContributionsEndpoints, definedContributionsPathDefinitions };
