import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const appPathDefinitions = {
  userFeedback: '/userFeedback',
  users: '/users',
};

const appEndpoints = {
  userFeedback: () =>
    buildEndpoint(minbankBaseUrl, appPathDefinitions.userFeedback),

  users: () => buildEndpoint(minbankBaseUrl, appPathDefinitions.users),
};

export { appEndpoints, appPathDefinitions };
