import { AccountProviderType } from '@pm-netbank/constants';
import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const accountsPathDefinitions = {
  accountBalance: '/accounts/v3/accounts/{accountIdentifier}/balance',
  accountDetails: '/accounts/v1/accounts/{accountNumber}/details',
  accounts: '/accounts/v4/accounts?providerType={providerType}',
  balances: '/accounts/v3/accounts/balances',
  loanUnsecuredInfo: '/accounts/v1/loans/unsecured/info',
  mainCategory:
    '/accounts/v1/accounts/pfm/spendings/categories/main/{mainCategoryCode}',
  metaCategory:
    '/accounts/v1/accounts/pfm/spendings/categories/meta/{metaCategoryCode}',
  spendingOverview:
    '/accounts/v2/accounts/pfm/spendings/overview?fromDate={fromDate}&toDate={toDate}',
  updateAccountsSettings: '/accounts/v3/accounts/settings',
  pfmTransactions:
    '/accounts/v2/accounts/pfm/spendings/transactions?merchantName={merchantName}&fromDate={fromDate}&toDate={toDate}&screenType={screenType}&pageSize={pageSize}&subCategoryCode={subCategoryCode}&page={page}&subscriptionId={subscriptionId}&transactionType={transactionType}',
  transactionAction: '/accounts/v2/accounts/pfm/spendings/transactions/action',
};

const accountsEndpoints = {
  accountBalance: (accountIdentifier: string) =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.accountBalance, {
      accountIdentifier,
    }),

  accountDetails: (accountNumber: string) =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.accountDetails, {
      accountNumber,
    }),

  accounts: (
    providerType: AccountProviderType = AccountProviderType.INTERNAL
  ) =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.accounts, {
      providerType: providerType,
    }),

  balances: () =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.balances),

  loanUnsecuredInfo: () =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.loanUnsecuredInfo),

  mainCategory: (mainCategoryCode?: string) =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.mainCategory, {
      mainCategoryCode: mainCategoryCode || ':mainCategoryCode',
    }),

  metaCategory: (metaCategoryCode?: string) =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.metaCategory, {
      metaCategoryCode: metaCategoryCode || ':metaCategoryCode',
    }),

  spendingOverview: (fromDate?: string, toDate?: string) =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.spendingOverview, {
      fromDate: fromDate || ':fromDate',
      toDate: toDate || ':toDate',
    }),

  updateAccountsSettings: () =>
    buildEndpoint(
      minbankBaseUrl,
      accountsPathDefinitions.updateAccountsSettings
    ),

  pfmTransactions: (
    screenType: string,
    fromDate: string,
    toDate: string,
    subCategoryCode?: string,
    merchantName?: string,
    subscriptionId?: string,
    transactionType?: string
  ) =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.pfmTransactions, {
      screenType,
      fromDate,
      toDate,
      subCategoryCode,
      merchantName,
      subscriptionId,
      transactionType,
    }),
  transactionAction: () =>
    buildEndpoint(minbankBaseUrl, accountsPathDefinitions.transactionAction),
};

export { accountsEndpoints, accountsPathDefinitions };
