import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const cardsPathDefinitions = {
  cardAction: '/cards/v2/{cardId}/action',
  cards: '/cards/v4/cards',
  cardUsageLimit: '/cards/v1/{cardId}/usagelimit',
  cardOrderStatus: '/cards-v2/v1/cards-v2/order-status',
};

const cardsEndpoints = {
  cardAction: (cardId: string) =>
    buildEndpoint(minbankBaseUrl, cardsPathDefinitions.cardAction, { cardId }),

  cards: () => buildEndpoint(minbankBaseUrl, cardsPathDefinitions.cards),

  cardUsageLimit: (cardId: string) =>
    buildEndpoint(minbankBaseUrl, cardsPathDefinitions.cardUsageLimit, {
      cardId,
    }),

  cardOrderStatus: () =>
    buildEndpoint(minbankBaseUrl, cardsPathDefinitions.cardOrderStatus),
};

export { cardsEndpoints, cardsPathDefinitions };
