import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const pfmPathDefinitions = {
  timeframes: '/pfm/v1/pfm/timeframes',
};

const pfmEndpoints = {
  timeframes: () =>
    buildEndpoint(minbankBaseUrl, pfmPathDefinitions.timeframes),
};

export { pfmEndpoints, pfmPathDefinitions };
