import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const externalProvidersPathDefinitions = {
  externalProviders: '/externalaccounts/v1/external/providers',
};

const externalProvidersEndpoints = {
  externalProviders: () =>
    buildEndpoint(
      minbankBaseUrl,
      externalProvidersPathDefinitions.externalProviders
    ),
};

export { externalProvidersEndpoints, externalProvidersPathDefinitions };
