import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const huntingPathDefinitions = {
  customerCheck: '/besok_hunting/hunting/customer/getcustomercheck',
  caseConsent: '/besok_hunting/hunting/customer/getcaseconsent?caseid={caseId}',
  altinnTaxStatus: '/besok_hunting/hunting/customer/getaltinntaxstatus',
  saveCaseConsent: '/besok_hunting/hunting/customer/savecaseconsent',
};

const huntingEndpoints = {
  customerCheck: () =>
    buildEndpoint(minbankBaseUrl, huntingPathDefinitions.customerCheck),
  caseConsent: (caseId: string) =>
    buildEndpoint(minbankBaseUrl, huntingPathDefinitions.caseConsent, {
      caseId,
    }),
  altinnTaxStatus: () =>
    buildEndpoint(minbankBaseUrl, huntingPathDefinitions.altinnTaxStatus),
  saveCaseConsent: () =>
    buildEndpoint(minbankBaseUrl, huntingPathDefinitions.saveCaseConsent),
};

export { huntingEndpoints, huntingPathDefinitions };
