import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const bankIdPathDefinitions = {
  populate: '/bank-id-kids/v1/populate',
  process: '/bank-id-kids/v1/process',
};

const bankIdEndpoints = {
  populate: () => buildEndpoint(minbankBaseUrl, bankIdPathDefinitions.populate),
  process: () => buildEndpoint(minbankBaseUrl, bankIdPathDefinitions.process),
};

export { bankIdEndpoints, bankIdPathDefinitions };
