import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const eInvoicePathDefinitions = {
  forwards: '/einvoice/v1/einvoices/forwards',
  singleForward: '/einvoice/v1/einvoices/forwards/{id}',
  acrossBankConsent: '/einvoice/v1/einvoices/acrossbank-consent',
};

const eInvoiceEndpoints = {
  forwards: () =>
    buildEndpoint(minbankBaseUrl, eInvoicePathDefinitions.forwards),

  singleForward: (id: string) =>
    buildEndpoint(minbankBaseUrl, eInvoicePathDefinitions.singleForward, {
      id,
    }),
  acrossBankConsent: () =>
    buildEndpoint(minbankBaseUrl, eInvoicePathDefinitions.acrossBankConsent),
};

export { eInvoiceEndpoints, eInvoicePathDefinitions };
