import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const subscriptionsPathDefinitions = {
  hideSubscription:
    '/subscriptions/v1/subscriptions/{subscriptionId}/hide?accountIdentifier={accountIdentifier}',
  subscriptions: '/subscriptions/v1/subscriptions',
};

const subscriptionsEndpoints = {
  hideSubscription: (subscriptionId: string, accountIdentifier: string) =>
    buildEndpoint(
      minbankBaseUrl,
      subscriptionsPathDefinitions.hideSubscription,
      { subscriptionId, accountIdentifier }
    ),

  subscriptions: () =>
    buildEndpoint(minbankBaseUrl, subscriptionsPathDefinitions.subscriptions),
};

export { subscriptionsEndpoints, subscriptionsPathDefinitions };
