import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const mortgageCalculatorPathDefinitions = {
  eligibility: '/mortgage/v1/mortgage/calculator/eligibility',
  parameters: '/mortgage/v1/mortgage/calculator/parameters',
  calculate: '/mortgage/v1/mortgage/calculator/calculate',
};

const mortgageCalculatorEndpoints = {
  eligibility: () =>
    buildEndpoint(
      minbankBaseUrl,
      mortgageCalculatorPathDefinitions.eligibility
    ),
  parameters: () =>
    buildEndpoint(minbankBaseUrl, mortgageCalculatorPathDefinitions.parameters),
  calculate: () =>
    buildEndpoint(minbankBaseUrl, mortgageCalculatorPathDefinitions.calculate),
};

export { mortgageCalculatorEndpoints, mortgageCalculatorPathDefinitions };
