import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const customerPathDefinitions = {
  customerDetails: '/customer/v2/details',
  search: '/customers/v1/customers/search',
};

const customerEndpoints = {
  customerDetails: () =>
    buildEndpoint(minbankBaseUrl, customerPathDefinitions.customerDetails),

  search: () => buildEndpoint(minbankBaseUrl, customerPathDefinitions.search),
};

export { customerEndpoints, customerPathDefinitions };
