import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;
const transactionsBaseUrl = '/transactions';

const transactionsPathDefinitions = {
  base: transactionsBaseUrl,
  transactionDetails: `${transactionsBaseUrl}/v1/{accountIdentifier}/transactions/{transactionId}`,
  transactions: `${transactionsBaseUrl}/v1/{accountIdentifier}/transactions?pageSize=25`,
};

const transactionsEndpoints = {
  base: () => buildEndpoint(minbankBaseUrl, transactionsPathDefinitions.base),

  transactionDetails: (accountIdentifier: string, transactionId: string) =>
    buildEndpoint(
      minbankBaseUrl,
      transactionsPathDefinitions.transactionDetails,
      { accountIdentifier, transactionId }
    ),

  transactions: (accountIdentifier: string) =>
    buildEndpoint(minbankBaseUrl, transactionsPathDefinitions.transactions, {
      accountIdentifier,
    }),
};

export { transactionsEndpoints, transactionsPathDefinitions };
