import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const insurancesPathDefinitions = {
  insuranceAgreementDetails: '/insurances/v1/insurances/agreements/details',
  insuranceAgreements: '/insurances/v2/insurances/agreements',
  insuranceAgreementDocument:
    '/insurances/v3/insurances-v2/agreements/document',
};

const insurancesEndpoints = {
  insuranceAgreementDetails: () =>
    buildEndpoint(
      minbankBaseUrl,
      insurancesPathDefinitions.insuranceAgreementDetails
    ),

  insuranceAgreements: () =>
    buildEndpoint(
      minbankBaseUrl,
      insurancesPathDefinitions.insuranceAgreements
    ),

  insuranceAgreementDocument: () =>
    buildEndpoint(
      minbankBaseUrl,
      insurancesPathDefinitions.insuranceAgreementDocument
    ),
};

export { insurancesEndpoints, insurancesPathDefinitions };
