import { FundHistoricPriceIntervalType } from '@pm-netbank/constants';
import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const spareBaseUrl = BasePaths.Spare;
const fundsBaseUrl = BasePaths.Funds;

const epkFlexPathDefinitions = {
  assetAllocationProposal: '/epk-flex/asset-allocation-proposal',
  eligibility: '/epk-flex/onboarding/eligibility',
  onboarding: '/epk-flex/onboarding',
  onboardingStatus: '/epk-flex/onboarding/status',
  questionnaire: '/epk-flex/onboarding/questionnaire',
  flexProducts: '/epk-flex/flex-products',
  fundDetails: '/funds/{isin}',
  fundHistoricPricesWithIntervalType:
    '/funds/{isin}/historic-prices?timeIntervalType={timeIntervalType}',
  funds: '/funds',
  selvvalgtEligibleFunds: '/funds?selvvalgtEligible=true',
};

const epkFlexEndpoints = {
  assetAllocationProposal: () =>
    buildEndpoint(spareBaseUrl, epkFlexPathDefinitions.assetAllocationProposal),

  eligibility: () =>
    buildEndpoint(spareBaseUrl, epkFlexPathDefinitions.eligibility),

  onboarding: () =>
    buildEndpoint(spareBaseUrl, epkFlexPathDefinitions.onboarding),

  onboardingStatus: () =>
    buildEndpoint(spareBaseUrl, epkFlexPathDefinitions.onboardingStatus),

  questionnaire: () =>
    buildEndpoint(spareBaseUrl, epkFlexPathDefinitions.questionnaire),

  flexProducts: () =>
    buildEndpoint(spareBaseUrl, epkFlexPathDefinitions.flexProducts),

  funds: () => buildEndpoint(fundsBaseUrl, epkFlexPathDefinitions.funds),

  selvvalgtEligibleFunds: () =>
    buildEndpoint(fundsBaseUrl, epkFlexPathDefinitions.selvvalgtEligibleFunds),

  fundHistoricPricesWithIntervalType: (
    isin: string,
    timeIntervalType: FundHistoricPriceIntervalType
  ) =>
    buildEndpoint(
      fundsBaseUrl,
      epkFlexPathDefinitions.fundHistoricPricesWithIntervalType,
      { isin, timeIntervalType }
    ),

  fundDetails: (isin: string) =>
    buildEndpoint(fundsBaseUrl, epkFlexPathDefinitions.fundDetails, { isin }),
};

export { epkFlexEndpoints, epkFlexPathDefinitions };
