import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const minbankBaseUrl = BasePaths.MinBank;

const paymentsPathDefinitions = {
  invoices: '/payments/v3/invoices',
  invoicesByCustomerId: '/payments/v3/invoices?customerId={customerId}',
  invoiceApprove: '/payments/v2/payments/invoice',
  invoiceApproveOnBehalfOf:
    '/payments/v2/payments/invoice?customerId={customerId}',
  invoiceDelete: '/payments/v3/invoices/{einvoiceId}',
  invoiceDeleteOnBehalfOf:
    '/payments/v3/invoices/{einvoiceId}?customerId={customerId}',
  invoiceAttachment: '/payments/v3/invoices/{einvoiceId}/url',
  invoiceAttachmentByCustomerId:
    '/payments/v3/invoices/{einvoiceId}/url?customerId={customerId}',
  ocrValidationRules: '/payments/v2/validation/accounts/ocr/rules',
  payment: '/payments/v5/payments/{paymentId}',
  paymentEdit: '/payments/v4/payments/domestic/{paymentId}',
  payments: '/payments/v5/payments',
  paymentsTransfer: '/payments/v4/payments/transfer',
  standingOrderRecipients: '/payments/v1/recipients',
  standingOrderAction: '/payments/v2/standing-orders/{agreementId}',
  standingOrders: '/payments/v2/standing-orders',
};

const paymentsEndpoints = {
  invoiceApprove: () =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.invoiceApprove),

  invoiceApproveOnBehalfOf: (customerId: string) =>
    buildEndpoint(
      minbankBaseUrl,
      paymentsPathDefinitions.invoiceApproveOnBehalfOf,
      { customerId }
    ),

  invoiceDelete: (einvoiceId: string) =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.invoiceDelete, {
      einvoiceId,
    }),

  invoiceDeleteOnBehalfOf: (einvoiceId: string, customerId: string) =>
    buildEndpoint(
      minbankBaseUrl,
      paymentsPathDefinitions.invoiceDeleteOnBehalfOf,
      { einvoiceId, customerId }
    ),

  invoiceAttachment: (einvoiceId: string) =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.invoiceAttachment, {
      einvoiceId,
    }),

  invoiceAttachmentByCustomerId: (einvoiceId: string, customerId: string) =>
    buildEndpoint(
      minbankBaseUrl,
      paymentsPathDefinitions.invoiceAttachmentByCustomerId,
      {
        einvoiceId,
        customerId,
      }
    ),

  invoices: () =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.invoices),

  invoicesByCustomerId: (customerId?: string) =>
    buildEndpoint(
      minbankBaseUrl,
      paymentsPathDefinitions.invoicesByCustomerId,
      {
        customerId,
      }
    ),

  ocrValidationRules: () =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.ocrValidationRules),

  payment: (paymentId: string) =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.payment, {
      paymentId,
    }),

  paymentEdit: (paymentId: string) =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.paymentEdit, {
      paymentId,
    }),

  payments: () =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.payments),

  paymentsTransfer: () =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.paymentsTransfer),

  paymentsRecurringTransfer: () =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.standingOrders),

  standingOrderRecipients: () =>
    buildEndpoint(
      minbankBaseUrl,
      paymentsPathDefinitions.standingOrderRecipients
    ),

  standingOrderAction: (agreementId: string) =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.standingOrderAction, {
      agreementId,
    }),

  standingOrders: () =>
    buildEndpoint(minbankBaseUrl, paymentsPathDefinitions.standingOrders),
};

export { paymentsEndpoints, paymentsPathDefinitions };
