import { buildEndpoint } from '../helpers/BuildEndpoint';
import BasePaths from './BasePaths';

const spareBaseUrl = BasePaths.Spare;

const pensionPathDefinitions = {
  agreements: '/agreements',
  agreementById: '/agreements/?agreementId={agreementId}',
  agreementDetails: '/agreements/{agreementId}',
};

const pensionEndpoints = {
  pensionAgreements: () =>
    buildEndpoint(spareBaseUrl, pensionPathDefinitions.agreements),

  pensionAgreementById: (agreementId: string) =>
    buildEndpoint(spareBaseUrl, pensionPathDefinitions.agreementById, {
      agreementId: agreementId,
    }),

  pensionAgreementDetails: (agreementId: string) =>
    buildEndpoint(spareBaseUrl, pensionPathDefinitions.agreementDetails, {
      agreementId: agreementId,
    }),
};

export { pensionEndpoints, pensionPathDefinitions };
